@use "base/variables" as v;
@use "base/mixins" as m;

.formulario {
  background-color: v.$secundario;
  width: min(60rem, 100%);
  margin: 0 auto 5rem auto;
  padding: 2rem;
  border-radius: 1rem;
}

.formulario fieldset {
  border: none;
  padding: 0 0;
}

.formulario legend {
  text-align: center;
  font-size: 1.5rem;
  text-transform: uppercase;
  font-weight: 700;
  margin-bottom: 1rem;
  color: v.$primario;
  @include m.tablet {
    font-size: 1.8rem;
    margin-bottom: 2rem;
  }
}

.boton {
  width: 100%;
}

@include m.tablet {
  .contenedor-campos {
    // width: 50%;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: auto auto 20rem;
    column-gap: 1rem;
  }

  .campo:nth-child(3),
  .campo:nth-child(4) {
    grid-column: 1 / 3;
  }

  .submit {
    display: flex;
    justify-content: flex-end;
  }

  input[type="submit"] {
    width: 50%;
  }
}

.campo {
  margin-bottom: 1rem;
}

.campo label {
  font-size: 1.4rem;
  color: v.$blanco;
  font-weight: bold;
  margin-bottom: 0.5rem;
  display: block;
  @include m.tablet {
    font-size: 1.6rem;
  }
}

.campo input,
.campo textarea {
  font-size: 1.3rem;
  width: 100%;
  border: none;
  padding: 1rem;
  border-radius: 0.5rem;
  @include m.tablet {
    font-size: 1.5rem;
    padding: 1.5rem;
  }
}

.campo textarea {
  height: 15rem;
  resize: none;
}
