@use "base/mixins" as m;
@use "base/variables" as v;

.titulo {
  .titulo-guion {
    display: none;
  }
  .titulo-texto {
    margin-top: 1rem;
    display: block;
  }
  @include m.tablet {
    .titulo-guion{
      display: inline;
    }
    .titulo-texto{
      margin-top: 0;
      display: inline;
    }
    // border-right: solid 3px v.$secundario;
    // white-space: nowrap;
    // overflow: hidden;
    margin-left: auto;
    margin-right: auto;
    // animation: animated-text 5s steps(29, end) 1s 1 normal both,
    //   animated-cursor 1000ms steps(29, end) infinite;
  }
  // span {
